import React, {Component} from 'react';
import axios from 'axios';
import {ContactContainer,
        ContactForm,
        ContactTitle,
        ContactLabel,
        ContactTextInput,
        ContactEmailInput,
        ContactTextAreaInput,
        ContactSubmit
} from '../Contact/ContactElement';

class ContactPage extends Component {

  
       
        state = {
            name: '',
            email: '',
            message: '',
            emailSent: false,

        }



   
        handleName=(e)=>{

            this.setState({

                name:e.target.value

            })


        }

        handleEmail=(e)=>{

            this.setState({

                email:e.target.value

            })


        }

        handleMessage=(e)=>{

            this.setState({

                message:e.target.value

            })


        }

        

    formSubmit = (e) => {
        e.preventDefault();

        let data = {
            name:this.state.name,
            email:this.state.email,
            message:this.state.message
        }

        axios.post('/api/form', data)
        .then(res=>{

            this.setState({
                emailSent:true,
            },this.resetForm())
        }).catch(()=>{
            console.log('message not sent');
        })


    }


    resetForm=()=>{

        this.setState({
            name:'',
            email:'',
            message:''

        })
        setTimeout(()=>{

            this.setState({
                emailSent:false,
            })

        }, 3000)


    }

    render(){

        return (

            <>
            
               <ContactContainer>
            
                <ContactTitle>Let's Talk</ContactTitle>
                <ContactForm id="form" onSubmit={this.formSubmit}>
                 <ContactLabel htmlFor="name">Full Name</ContactLabel>
                  <ContactTextInput onChange={this.handleName} id="name" type="text" name="fullname" value={this.state.name} placeholder="Your name..." required/>

                  <ContactLabel htmlFor="email">Email</ContactLabel>
                  <ContactEmailInput onChange={this.handleEmail} id="email" type="email" name="email" value={this.state.email} placeholder="Your email..." required/>

                  <ContactLabel htmlFor="message">Message</ContactLabel>
                  <ContactTextAreaInput onChange={this.handleMessage} id="message" name="message" value={this.state.message} placeholder="Please write something here..." required/>

                  <ContactSubmit type="submit" form="form" value="Submit">Send message</ContactSubmit>{this.state.emailSent ? <span style={{color:'green'}}>Email sent successfully!</span> : <span></span>}

                  
                  
                </ContactForm>  
         
                </ContactContainer> 

            

            </>
        )

    }
    
}

export default ContactPage
